export default {
	data() {
		return {
			show:false,
			techerName: ''
		}
	},
	methods: {
		backPage () {
			uni.navigateBack({
				delta: 1
			})
		},
		showTeacher (val) {
			this.show = !this.show
			if (val == 1) {
				this.$data.techerName = '李铮'
				console.log(val)
			} else if (val == 2) {
				this.$data.techerName = '海城电子'
				console.log(val)
			} else if ( val == 3 ) {
				this.$data.techerName = '李凯'
				console.log(val)
			} else {
				this.$data.techerName = '肖宝平'
				console.log(val)
			}
			
		}
	}
}
